<template>
  <div id="app">
    <div class="app-wrapper">
      <div class="app-container">
        <!-- 侧边栏 -->
        <div class="sidebar">
          <div class="sidebar-box">
            <div
              class="menu-item"
              v-for="(item, index) in docs"
              :key="index"
            >
              <!-- delete -->
              <span
                class="mark"
                style="background-color:#F56C6C"
                v-if="item['method']==='DELETE'"
              ></span>
              <!-- put -->
              <span
                class="mark"
                style="background-color:#E6A23C"
                v-if="item['method']==='PUT'"
              ></span>
              <!-- get -->
              <span
                class="mark"
                style="background-color:#409EFF"
                v-if="item['method']==='GET'"
              ></span>
              <!-- post -->
              <span
                class="mark"
                style="background-color:#67C23A"
                v-if="item['method']==='POST'"
              ></span>
              <!-- 未填写方法 -->
              <span
                class="mark"
                style="background-color:#303133"
                v-if="item['method']===''"
              ></span>
              <a
                class="list-item"
                :href="'#'+item['title']"
              >{{item['title']}}</a>
            </div>
          </div>
        </div>
        <!--  -->
        <div class="index-content">
          <Index
            style="margin-top:8px"
            v-for="(item, index) in docs"
            :key="index"
            :data="item"
          ></Index>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Index from "./components/Index";
export default {
  name: "app",
  components: {
    Index
  },
  data() {
    return {
      testData: [],
      code: "",
      docs: []
    };
  },
  created() {
    <div c></div>;
    this.fetchTestData();
  },
  methods: {
    processParams(_param) {
      let params = [];
      if (Array.isArray(_param)) {
        _param.forEach(_p => {
          let _ps = _p.split(":");
          params.push({
            name: _ps[0] || "",
            type: _ps[1] || "",
            explain: _ps[2] || "",
            remark: _ps[3] || "",
            other: _ps[4] || "",
            value: "" //存储输入值
          });
        });
      }
      return params;
    },

    filter(v) {
      if (v === "") {
        return false;
      }
      return true;
    },

    hasKey(_v) {
      if (!_v) {
        return false;
      }
      return true;
    },

    fetchTestData() {
      // console.log(this.$doc_server);
      this.$http
        .get(`http://${this.$doc_server.addr}:${this.$doc_server.port}/doc/v1`)
        .then(res => {
          let body = res.data.Docs;
          // console.log(body);
          if (body !== null && Array.isArray(body)) {
            body.forEach(e => {
              let params = e["Param"];
              params.forEach(p => {
                let param = {
                  title: this.hasKey(p["@title"]) ? p["@title"][0] : "",
                  url: this.hasKey(p["@url"]) ? p["@url"][0] : "",
                  header: this.hasKey(p["@header"])
                    ? p["@header"][0]
                    : "multipart/form-data",
                  method: this.hasKey(p["@method"])
                    ? p["@method"][0].toUpperCase()
                    : "",
                  params: this.processParams(p["@param"]),
                  result: "" //存储测试结果
                };
                if (!param["header"]) {
                  param["header"] = "multipart/form-data";
                }
                this.filter(param["url"]) && this.docs.push(param);
              });
            });
          }
        })
        .catch(err => {
          console.log(err);
        });
    }
  }
};
</script>

